import { initializeApp } from "firebase/app";
import { getAuth, GoogleAuthProvider, signInWithPopup } from "firebase/auth";

const firebaseConfig = {
  apiKey: "AIzaSyBoI95Ck0nIjfbW05TU5mJmauY7eUHDUSs",
  authDomain: "authtoplanning.firebaseapp.com",
  projectId: "authtoplanning",
  storageBucket: "authtoplanning.appspot.com",
  messagingSenderId: "243914694472",
  appId: "1:243914694472:web:0d820bd421bb77ca48dd7d"
};

// Initialize Firebase
const app = initializeApp(firebaseConfig);

export const auth = getAuth(app);
const provider = new GoogleAuthProvider();

export const signInWithGoogle = async () => {
    await signInWithPopup(auth, provider)
        .then(result => {
            localStorage.setItem("name", result.user.displayName);
            localStorage.setItem("email", result.user.email);
            localStorage.setItem("photo", result.user.photoURL);
        })
        .catch(err => console.log(err));
}