import React, { useState } from "react";
import io from "socket.io-client";
import './App.css';
import Poker from './Poker';
import { signInWithGoogle } from "./firebase";

function App() {
    const [loggedIn, setLoggedIn] = useState(false);
    const [username, setUsername] = useState('');
    const [user, setUser] = useState({});
    const [room, setRoom] = useState('');
    const [socket, setSocket] = useState({});
            
    const connectToRoom = () => {
        if (username !== '') {
            setUser({
                name: username,
                email: "",
                photo: '../favicon.png'
            });
            setLoggedIn(true);
            setSocket(io(process.env.REACT_APP_SOCKET_URL,  { transports : ['websocket'] }));
        }
    }

    const connectWithGoogle = () => {
        if (localStorage.getItem('name') !== null && localStorage.getItem('name') !== '') {
            setUser({
                name: localStorage.getItem('name'),
                email: localStorage.getItem('email'),
                photo: localStorage.getItem('photo'),
            });
            setSocket(io(process.env.REACT_APP_SOCKET_URL,  { transports : ['websocket'] }));
            setLoggedIn(true);
        } else {
            signInWithGoogle()
                .then(result => {
                    setUser({
                        name: localStorage.getItem('name'),
                        email: localStorage.getItem('email'),
                        photo: localStorage.getItem('photo'),
                    });
                    setSocket(io(process.env.REACT_APP_SOCKET_URL,  { transports : ['websocket'] }));
                    setLoggedIn(true);
                });
        }
        
    }
    
    return (
        <div className="App">
        {!loggedIn ? (
            <div id="loginform">
                <input type="text" name="user" id="user" placeholder="Nome..." autoComplete="off" onChange={(e) => {setUsername(e.target.value)}}/>
                <button onClick={connectToRoom}>Enter</button>
                <span>OR</span>
                <button className="login-with-google-btn" onClick={connectWithGoogle}>Sign in with Google</button>
            </div>
        ) : <Poker user={user} room={window.location.search.replace('?', '')} socket={socket}/>
        }
        </div>        
    );
}

export default App;
